import React from "react"
import {cssVars, ThemeContext, ThemeProvider} from "../utils/theme-provider";
import {ThemeSelector} from "./theme-selector-button";
import {Helmet} from "react-helmet";
import {darkTheme, lightTheme} from "../gatsby-plugin-theme-ui";


export default function Layout({children}) {
    return (
        <ThemeProvider>
            <HelmetWrapper/>
            <div style={{
                margin: `3rem auto`,
                maxWidth: '33vw',
                padding: `0 1rem`,
                color: cssVars.normalTextColour,
            }}>
                <ThemeSelector/>
                {children}
            </div>
        </ThemeProvider>
    )
}

const HelmetWrapper = () => {
    const {colorMode, setColorMode} = React.useContext(ThemeContext);
    const darkModeSelected = colorMode === 'dark';

    if (darkModeSelected) {
        return <Helmet>
            <style>{`body { background-color: ${darkTheme.background}; }`}</style>
        </Helmet>

    }

    return <Helmet>
        <style>{`body { background-color: ${lightTheme.background}; }`}</style>
    </Helmet>

}